// Angular
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
// Components
import { BaseComponent } from '../metronic/views/theme/base/base.component';
import { ErrorPageComponent } from '../metronic/views/theme/content/error-page/error-page.component';
// Auth
import { AuthGuard } from '../metronic/core/auth';
// Module
import { HomeModule } from '../app/views/home/home.module';
import { CampaignsModule } from '../app/views/campaigns/campaigns.module';
import { StatisticsModule } from '../app/views/statistics/statistics.module';

const routes: Routes = [
  {
    path: 'auth',
    loadChildren: () =>
      import('../metronic/views/pages/auth/auth.module').then(m => m.AuthModule)
  },

  {
    path: '',
    component: BaseComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'dashboard',
        loadChildren: () => HomeModule
      },
      {
        path: 'campaigns',
        loadChildren: () => CampaignsModule
      },
      {
        path: 'mail',
        loadChildren: () =>
          import('../metronic/views/pages/apps/mail/mail.module').then(
            m => m.MailModule
          )
      },
      {
        path: 'ecommerce',
        loadChildren: () =>
          import(
            '../metronic/views/pages/apps/e-commerce/e-commerce.module'
          ).then(m => m.ECommerceModule)
      },
      {
        path: 'ngbootstrap',
        loadChildren: () =>
          import('../metronic/views/pages/ngbootstrap/ngbootstrap.module').then(
            m => m.NgbootstrapModule
          )
      },
      {
        path: 'material',
        loadChildren: () =>
          import('../metronic/views/pages/material/material.module').then(
            m => m.MaterialModule
          )
      },
      {
        path: 'user-management',
        loadChildren: () =>
          import(
            '../metronic/views/pages/user-management/user-management.module'
          ).then(m => m.UserManagementModule)
      },
      {
        path: 'wizard',
        loadChildren: () =>
          import('../metronic/views/pages/wizard/wizard.module').then(
            m => m.WizardModule
          )
      },
      {
        path: 'builder',
        loadChildren: () =>
          import('../metronic/views/theme/content/builder/builder.module').then(
            m => m.BuilderModule
          )
      },
      {
        path: 'error/403',
        component: ErrorPageComponent,
        data: {
          type: 'error-v6',
          code: 403,
          title: '403... Access forbidden',
          desc:
            "Looks like you don't have permission to access for requested page.<br> Please, contact administrator"
        }
      },
      { path: 'error/:type', component: ErrorPageComponent },
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      { path: '**', redirectTo: 'dashboard', pathMatch: 'full' }
    ]
  },

  { path: '**', redirectTo: 'error/403', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
