// Angular
import { Component, OnInit } from '@angular/core';
// Layout
import { LayoutConfigService } from '../../../../core/_base/layout';
// Object-Path
import * as objectPath from 'object-path';

@Component({
	selector: 'kt-topbar',
	templateUrl: './topbar.component.html',
	styleUrls: ['./topbar.component.scss'],
})
export class TopbarComponent {
	searchDropdown:boolean;
	notification:boolean;
	quickAction:boolean;
	cart:boolean;
	quickPanel:boolean;
	languageSelector:boolean;
	userProfile:boolean;
	/**
	 * Component constructor
	 *
	 * @param layoutConfigService: LayouConfigService
	 */
	constructor(private layoutConfigService: LayoutConfigService) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit(): void {
		const config = this.layoutConfigService.getConfig();

		// footer width fluid
		 ;
		 this.searchDropdown=objectPath.get(config, 'header.topbar.search.display');
		 this.notification=objectPath.get(config, 'header.topbar.notifications.display');
		 this.quickAction=objectPath.get(config, 'header.topbar.quick-action.display');
		 this.cart=objectPath.get(config, 'header.topbar.cart.display');
		 this.quickPanel=objectPath.get(config, 'header.topbar.quick-panel.display');
		 this.languageSelector=objectPath.get(config, 'header.topbar.languages.display');
		 this.userProfile=objectPath.get(config, 'header.topbar.user.display');
	}
 }
